.Navbar_Container{
    height: 100vh;
    background-color: black;
    opacity: .6;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-start;
    text-align: left;
    z-index: 9999;
    margin-bottom: calc(-100vh - 50px);
    padding: 50px;
    position: sticky;
    top: 0;
    margin-top: -50px;
    a{
        color: white !important;
        font-size: 80px;
        cursor: pointer;
        margin: 10px 0;
        text-decoration: none;
    }
    p{
        color: white;
        font-size: 40px;
        cursor: pointer;
    }
}