.Column_Container{
    display: flex;
    flex-direction: column;
    margin: 0 20px;
    .text1{
        font-size: 20px;
        color: #C4C5C5;
        margin-bottom: 10px;

    }
    .text2{
        font-size: 20px;
        color: #F5CC7A;
        margin-top: 0;
        font-weight: bold;
    }
}

.Info_Container{
    flex-direction: column;
    justify-content: center;
    padding: 0 150px;

    .row1{
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
        .logos{
            margin: 20px;
        }
    }
   h1{
       margin-top: 100px;
       margin-bottom: 20px;
       color: white;
   }
   .spinner {
    width: 40px;
    height: 40px;
    border: 5px solid #F4CC79;
    border-left-color: #151719;
    border-radius: 50%;
    background: transparent;
    animation-name: rotate-s-loader;
    animation-iteration-count: infinite;
    animation-duration: 1s;
    animation-timing-function: linear;
    position: relative;
  }
   
}
@keyframes rotate-s-loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
// ----------------- Media Query ------------------- //

@media only screen and (max-width: 800px) {
    .Info_Container{
        padding: 0 30px;
    }
    
}