.Description_Container{
    display: flex;
    justify-content: center;
    margin-top: 100px;
    h2{
        color: white;
        margin: 0;
        font-size: 33px;
        font-weight: 200;
        text-align: right;
        line-height: .8em;
        span{
            color: #F5BC58;
            font-weight: bold;
            font-size: 30px;
        }
    }
    .body{
        display: flex;
        flex-direction: column;
        text-align: left;
        div{
            margin-bottom: 20px;
        }
        margin-left: 40px;
        p{
            max-width: 500px;
            color: white;
        }
        h3{
            color: #F3C87F;
            font-size: 28px;
            line-height: .2em;
            margin: 0;
        }
    }
}
// ----------------- Media Query ------------------- //

@media only screen and (max-width: 800px) {
    .Description_Container{
        flex-direction: column;
        align-items: flex-start;
        h2{
            margin-bottom: 50px;
            margin-left: 40px;
            text-align: left;
        }
        .body{
            h3{
                line-height: .9em;
            }
            p{
                max-width: 80vw;
            }
        }
    }
    
}