.Header_Container{
    background-image: linear-gradient(rgba(255, 255, 255, .1), rgba(0, 0, 0, .3)),  url('../../assets/images/background2.jpeg');
    background-blend-mode: normal;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 70vh;
    padding-top: 100px;
    margin-bottom: 20px;
    border-bottom: 0px solid blue;
    .logo{
        justify-self: center;
        margin-left: 20px;
    }
    .row{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .social_icons{
        display: flex;
        width: 100%;
        justify-content: space-around;
    }
    .firstRow{
        display: flex;
        justify-content: space-around;
        align-items: flex-start;
        height: 50vh;
    }
    .logo2{
        margin-left: 20px;
        justify-self: flex-start;
    }
    button{
        border-radius: 5px;
        justify-self: center;
        animation-name: stretchs;
        position: absolute;
        animation-duration: 2s;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
        border: none;
    }
    .animate2{
        animation-name: stretchs2;
        opacity: .4;
        animation-duration: 2s;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
    }
    a{
        text-decoration: none;
    }
    div{
        display: flex;
    }
   
}

.pentagon {
    width: 100vw;
    align-self: flex-end;
    height: 115px;
  }
  .pentagon:before {
    content: "";
    position: absolute;
    height: 0;
    width: 0vw;
    border-width: 0 51vw 115px 0vw;
    border-style: solid;
    border-color: transparent transparent #151719;
  }
  .pentagon:after {
    content: "";
    position: absolute;
    height: 0;
    width: 0vw;
    border-width: 0 50vw 115px 0vw;
    border-style: solid;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
    right: 0;
    border-color: transparent transparent #151719;
  }
  // ----------------- Media Query ------------------- //

@media only screen and (max-width: 700px) {
    .Header_Container{
        .logo{
            display: none;
        }
        .social_icons{
            justify-self: flex-start;
        }
       
    }
    
}
@media only screen and (min-width: 701px) {
    .Header_Container{
        .logo2{
            display: none;
        }
    }
    
}