.RoadMapCard_Container{
    padding: 50px 30px 50px 50px;
    width: 500px;
    height: 150px;
    background-color: rgb(71, 72, 74);
    background-size: 100% 100%;
    margin: 90px 0;
    max-width: 70vw;
    margin-bottom: -24px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .shapes{
        display: flex;
        flex-direction: column;
        position: absolute;
        z-index: 999;
        align-items: center;
        hr{
            width: 1px;
            border: none;
            background: linear-gradient(to right, #EDAE3F , #F1C87F);
        }
        .hr1{
            height: 140px;
            z-index: 99;
        }
        .hr2{
            z-index: 99;
            height: 185px;
        }
        .rhombus{
            width: 15px;
            height: 15px;
            background: linear-gradient(to right, #EDAE3F , #F1C87F);
            transform: rotate(45deg);
        }
    }
    .description{
        margin-left: 50px;
    }
    p{
        line-height: 1.8em !important;
    }
    h3{
        color: #F1C87F;
    }
}
// ----------------- Media Query ------------------- //

@media only screen and (max-width: 600px) {
    .RoadMapCard_Container{
        padding-left: 30px;
        padding-right: 10px;
        .description{
            margin-left: 30px;
        }
        p{
            font-size: .8rem;
    
        }
    }
}
@media only screen and (max-width: 350px) {
    .RoadMapCard_Container{
        padding-left: 30px;
        padding-right: 10px;
        .description{
            margin-left: 30px;
        }
        p{
            font-size: .7rem;
    
        }
    }
}