.App {
  text-align: center;
}

.Secondary_Button{
  background-color: rgb(71, 72, 74);
  border: none;
  color: white;
  font-size: 18px;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}
.menu{
  position: sticky;
  top: 10px;
}
.icons{
  margin: 0 5px;
  color: black !important;
  padding: 9px;
  display: flex;
  align-items: center;
  background-color: white;
  height: 30px;
  border-radius: 5px;
  z-index: 999;
  cursor: pointer;
  img{
      height: 30px;
      padding: 0 3px;
  }
}

@keyframes stretchs {
  0% { padding: 15px 80px; }
  80% {  padding: 20px 85px; margin-left: -5px; margin-top: -5px; font-size: 1.1rem; }
  100% {  padding: 15px 80px;  }  
}  

// ----------------- Media Query ------------------- //

@media only screen and (max-width: 800px) {
  .iconsmobile{
    padding: 0 ;  
    height: 30px !important;
    width: 30px !important;
  }
   @keyframes stretchs {
      0% { padding: 12px 50px; }
      60% {  padding: 15px 55px; margin-left: -5px; margin-top: -5px; font-size: 1.1rem; }
      100% {  padding: 12px 50px;  }  
  }  
}
@media only screen and (max-width: 600px) {
  .icons2{
    margin: 0 5px;
    padding: 5px 3px;
    height: 25px;
    img{
        height: 25px;
        padding: 0 3px;
    }
  }
}
