.AnimatedDescription_Container{
    margin:100px 0px ;
    display: flex;
    justify-content: center;
    h1{
        width: 270px;
        margin: 0;
        margin-top: -3px;
        text-align: right;
        color: white;
        span{
             color: #F1B54D;
        }
    }
    
    div{
        display: flex;
        flex-direction: column;
        margin-left: 30px;
        border-left: 2px solid #47484A;
        padding-left: 30px;
        p{
            width: 30vw;
            margin: 0;
            color: white;
            text-align: left;
            font-size: 18px;
            line-height: 1.5em;
         }
         button{
             align-self: flex-start;
             margin-top: 25px;
             background-color: rgb(71, 72, 74);
             border: none;
             color: white;
             font-size: 1rem;
             padding: 10px 20px;
             border-radius: 5px;
         }
    }
}
  // ----------------- Media Query ------------------- //

  @media only screen and (max-width: 615px) {
    .AnimatedDescription_Container{
        flex-direction: column;
        align-items: flex-start;
        padding: 0 30px;
        div{
            padding: 0;
            margin: 0;
            border: none;
            p{
                width: 90%;
            }
        }
        h1{
            text-align: left;
            margin-bottom: 20px;
            max-width: 80vw;
        }
       
    }
    
}