.TeamCard_Container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    margin: 50px 20px 0 20px;
    .member_image{
        width: 45vw;
        height: 45vw;
        max-width: 490px;
        max-height: 490px;
        object-fit: cover ;
        object-position: 0 50%;
    }
    a{
        text-decoration: none;
        background-color: white;
        color: black;
        padding: 5px;
        border-radius: 5px;
        margin-left: 15px;
    }
    .social_media{
        display: flex;
        align-items: center;
    }
    .info{
        display: flex;
        justify-content: space-between;
        width: 45vw;
        max-width: 490px;
        max-height: 490px;
        h2{
            color: #FECC72;
            font-size: 28px;
            margin-bottom: 0;
        }
        p{
            margin-top: 5px;
            color: white;
            font-size: 20px;
            padding: 0;
            width: auto;
            text-align: left;
        }
    }
}
// ----------------- Media Query ------------------- //

@media only screen and (max-width: 800px) {
    .TeamCard_Container{
        margin: 50px 10px 0 10px;
    }
    
}
@media only screen and (max-width: 600px) {
    .TeamCard_Container{
        margin-bottom: 30px;
        .member_image{
            width: 90vw;
            height: 90vw;
        }
        .info{
            width: 90vw;
        }
        a{
            margin-left: 5px;
        }
    }
    
}