.TeamSection_Container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
}
// ----------------- Media Query ------------------- //

@media only screen and (max-width: 600px) {
    .TeamSection_Container{
        grid-template-columns: repeat(1, 1fr);
    }
}